// Detail Box
#ctl00_ctl00_MainContent_ctl00_ProductINVBuyBoxControl_ProductDetailActionButtonControl1_ContainerPanelPanel {
  .productbuttons {
    margin: 0;
    padding: 0;
    background: 0 0;
  }
  .btn {
    width: 20rem;
    max-width: none;
    margin: 1rem auto;
    margin-left: 1.5rem;
    display: block;
  }
}
.ProductDetailBlockCell a.action-hyperlink:last-child {
    display: none;
}
#ctl00_MainContent_ctl00_ProductINVBuyBoxControl_ProductDetailActionButtonControl1_AddWishlistButton {
  @include unButton()
}

.productbuttons {
  .wishList {
    @include unButton();
    width: 100%;
  }
  .addToCart {
    width: 100%;
  }
}

#previewButton {
  width: auto;
}

@include break(max, $breakpoint-max-xs-override) {
    .productbuttons-mobile {
        .productbuttons {
            + .productbuttons {
                &:last-child {
                    margin-top: 0;
                    padding-top: 0;
                    padding-bottom: $gutter-width / 2;
                }
            }
        }
        .wishList {
          //needed a little extra specificty
            .ProductDetailControl_PersonifyDefault & {
              margin-top: $gutter-width/2;
              margin-bottom: 0;
            }   
        }
    }
}




// Ribbon
#ctl00_MainContent_ctl00_CartSavingsControls1_Container {
  border: none;
}

// Header
.ProductDetailControl_PersonifyDefault .ProductDetailNameDisplay {
  border: none;
}

// Layout
.ProductDetailControl_PersonifyDefault {
  .ProductDetailImageCell {
    .productImages {
      margin: auto;
      margin-top: 1.5rem;
    }
  }
  .ProductDetailImageCell {
    width: 25%;
    max-width: none;
    margin: 0;
    padding: 0 1.5rem;
  }
  .ProductDetailContentCell {
    width: 50%;
    max-width: none;
    margin: 0;
    padding: 0 1.5rem;
  }
  .productThumb {
    margin: 0;
    width: 33.333%;
    border: none;
    padding: 1rem;
  }
  .productThumb a {
    border: none;
  }
  .mainProductImage {
    border: none;
  }
  .ProductDetailLeftCell {
    width: 25%;
    margin: 0;
  }
  .ProductDetailBlockCell {
    max-width: 50%;
    margin-left: auto;
    margin-right: auto;
    padding: 0 1.5rem;
  }
  .productActions {
    border: none;
    max-width: none;
    padding: 1.5rem;
    min-width: 0;
    width: 100%;
    .quantityContainer .quantity {
      padding: .4rem;
      height: auto;
      border-color: $ift-blue;
      border-radius: 4px;
      transition: 300ms all;
      &:focus {
        transition: 300ms all;
        box-shadow: 0 0 4px #cbd0ef;
      }
    }
  }
  .savingsHeader {
    max-width: 20rem;
  }
}

.showLess.action-hyperlink:before {
   content: "-";
   line-height: .8;
}

.showMore.action-hyperlink:before {
   content: "+";
}

// Savings Block
.savingsHeader {
  position: relative;
  background-color: white;
  margin: 1rem auto;
  padding: 1rem;
  height: 4rem;
  max-width: 20rem;
  color: #333;
  .savingsLink {
    color: #333;
  }
  // &:before,&:after{
  //   content: "";
  //   position: absolute;
  //   border: 1rem solid transparent;
  //   left: -1rem;
  // }
  // &:before{
  //   top: 0;
  //   border-top: 2rem solid $ift-green-light;
  // }
  // &:after{
  //   bottom: 0;
  //   border-bottom: 2rem solid $ift-green-light;
  // }
  .decore {
    display: none;
  }
}

@media screen and (max-width: 990px) {
  .ProductDetailControl_PersonifyDefault {
    .ProductDetailImageCell {
      width: 100%;
    }
    .ProductDetailContentCell {
      width: 100%;
    }
    .ProductDetailLeftCell {
      width: 100%;
    }
  }
}
